import { Component, OnInit, ChangeDetectorRef, inject } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Observable, Subscription, BehaviorSubject, map, switchMap, tap, debounceTime, combineLatest, filter, startWith, forkJoin, of } from 'rxjs';
import { MatTableModule } from '@angular/material/table';
import { FormControl, ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MatInputModule } from '@angular/material/input';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatCardModule } from '@angular/material/card';
import { MatIconModule } from '@angular/material/icon';
import { MatButtonModule } from '@angular/material/button';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatSelectModule } from '@angular/material/select';
import { RouterModule } from '@angular/router';
import { SetLanguageService } from '../services/set-language.service';
import { RequestService } from '../services/request.service';
import { SelectedLangService } from '../selected-lang.service';

export interface WikibaseEntity {
  id: string;
  labels?: { [lang: string]: { value: string } };
  aliases?: { [lang: string]: { value: string }[] };
  descriptions?: { [lang: string]: { value: string } };
}

function chunkArray<T>(array: T[], chunkSize: number): T[][] {
  const results: T[][] = [];
  for (let i = 0; i < array.length; i += chunkSize) {
    results.push(array.slice(i, i + chunkSize));
  }
  return results;
}

@Component({
  selector: 'app-search',
  standalone: true,
  imports: [
    CommonModule,
    RouterModule,
    ReactiveFormsModule,
    FormsModule,
    MatInputModule,
    MatFormFieldModule,
    MatTableModule,
    MatIconModule,
    MatButtonModule,
    MatCardModule,
    MatAutocompleteModule,
    MatCheckboxModule,
    MatSelectModule
  ],
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit {
  private changeDetector = inject(ChangeDetectorRef);
  private request = inject(RequestService);
  private setLanguage = inject(SetLanguageService);
  private lang = inject(SelectedLangService);

  title = 'factgrid';
  subtitle: string = "a database for historians";
  advanced_search: string = "advanced search";
  projects: string = "research projects";
  fields: string = "fields of researcch";
  projectsInput: string = "search for projects";
  itemsInput: string = "search for items";
  filterResults: string = "filter results";

  researchFields: any[] = [];
  private researchFields$ = new BehaviorSubject<any[]>([]);
  searchResearchField = new FormControl('');
  filteredResearchFields$: Observable<any[]>;

  selectedResearchField: string = localStorage['selectedResearchField'];

  searchInput = new FormControl();
  showInDescription = false;
  showInDescription$ = new BehaviorSubject<boolean>(false);

  public selectedItem: Observable<any>;
  public isDisplay: boolean = false;

  data$ = new Observable<string[]>();
  searchQuery$ = new BehaviorSubject<string>('');
  labels: Subscription | undefined;
  items: any[] = [];
  items$ = new BehaviorSubject<any[]>([]);
  filteredItems$: Observable<any[]>;
  filterInput = new FormControl('');
  newItem: any;
  itemId: string;
  pages: any;
  hintValue$: Observable<number>;

  private baseGetURL = 'https://database.factgrid.de//w/api.php?action=wbgetentities&ids=';
  private getUrlSuffix = '&format=json&origin=*';

  formerVisitsTitle: string = "you have visited:";
  selectedItemsList: any[] = JSON.parse(localStorage.getItem('selectedItems'));

  ngOnInit(): void {
    this.subtitle = this.lang.getTranslation('subtitle', this.lang.selectedLang);
    this.advanced_search = this.lang.getTranslation('advanced_search', this.lang.selectedLang);
    this.projects = this.lang.getTranslation('projects', this.lang.selectedLang);
    this.fields = this.lang.getTranslation('fields', this.lang.selectedLang);
    this.projectsInput = this.lang.getTranslation('projectsInput', this.lang.selectedLang);
    this.itemsInput = this.lang.getTranslation('itemsInput', this.lang.selectedLang);
    this.formerVisitsTitle = this.lang.getTranslation('formerVisitsTitle', this.lang.selectedLang);
    this.filterResults = this.lang.getTranslation('filterResults', this.lang.selectedLang);

    this.selectedItemsList = this.selectedItemsList.filter(function (el) { return (el !== null) });
 //   this.pages = this.request.getStat().pipe(map(res => Object.values(res)[1].statistics.pages));
    //  this.pages.subscribe(res => console.log(res));
    this.pages = this.request.getStat().pipe(
      map(res => {
        console.log(res);
        const values = Object.values(res);
        // On vérifie que l'index 1 existe et contient bien statistics.pages
        if (values[1] && values[1].statistics && typeof values[1].statistics.pages === 'number') {
          return values[1].statistics.pages;
        }
        // Fallback : retourne 0 ou null si la structure n'est pas conforme
        return 0;
      })
    );


    const lang = this.lang.selectedLang;
    const ResearchFieldQuery = `https://database.factgrid.de/sparql?query=SELECT ?item ?itemLabel ?itemDescription  
    WHERE {
      SERVICE wikibase:label { bd:serviceParam wikibase:language "${lang},en". }
      ?item wdt:P2 wd:Q11295.
    }
    ORDER BY ?itemLabel
  `;

    this.filteredResearchFields$ = combineLatest([
      this.researchFields$,
      this.searchResearchField.valueChanges.pipe(startWith(''))
    ]).pipe(
      map(([fields, value]) => {
        const search = (value ?? '').toString().toLowerCase();
        return fields.filter(f => f.name.toLowerCase().includes(search));
      })
    );

    this.request.getList(ResearchFieldQuery)
      .pipe(
        map(res => this.listFromSparql(res)),
        map(res => [
          { name: 'all', id: 'Q0' },
          ...res.results.bindings.map(b => ({
            name: b.itemLabel.value,
            id: b.item.id,
            description: b.itemDescription?.value ?? ''
          }))
        ]),
   //     tap(res => console.log(res))
      )
      .subscribe(projects => {
        this.researchFields = projects;
        this.researchFields$.next(projects);
        this.updateHintValue();
      });

    this.searchResearchField.valueChanges.subscribe(value => {
      if (typeof value === 'string') {
        this.selectedResearchField = '';
      }
    });

    this.labels = combineLatest([
      this.searchInput.valueChanges.pipe(startWith('')),
      this.showInDescription$.asObservable()
    ]).pipe(
      debounceTime(250),
      tap(([label, showInDescription]) => {
        if (!label || label.length === 0) {
          this.items = [];
          this.items$.next([]);
          this.isDisplay = false;
          this.updateHintValue(); 
          this.changeDetector.detectChanges();
        }
      }),
      filter(([label, _]) => !!label && label.length > 0),
      switchMap(([label, showInDescription]) => {
        const searchTerm = (label || '').toLowerCase();

        // Correction ici : si "all" ou "Q0", pas de filtre projet
        let searchQuery = '';
        if (
          !this.selectedResearchField ||
          this.selectedResearchField === 'all' ||
          this.selectedResearchField === 'Q0'
        ) {
          searchQuery = searchTerm ? `${searchTerm}*` : '';
        } else {
          searchQuery = `haswbstatement:P131=${this.selectedResearchField}${searchTerm ? ' ' + searchTerm + '*' : ''}`;
        }

        const searchUrl =
          'https://database.factgrid.de/w/api.php' +
          '?action=query' +
          '&list=search' +
          '&format=json' +
          '&origin=*' +
          `&srsearch=${searchQuery}` +
          '&srnamespace=120' +
          '&srlimit=500';

        return this.request.getItem(searchUrl).pipe(
          map(res => {
            if (!res.query || !res.query.search) return [];
            const ids = res.query.search
              .map((item: any) => {
                const match = item.title.match(/Q\d+/);
                return match ? match[0] : null;
              })
              .filter((qid: string | null) => !!qid);
            return ids;
          }),
          filter((ids: string[]) => ids.length > 0),
          switchMap((ids: string[]) => {
            const chunks = chunkArray(ids, 50);
            const requests = chunks.map(chunk => {
              const idsParam = chunk.join('|');
              const getEntitiesUrl = `https://database.factgrid.de/w/api.php?action=wbgetentities&ids=${idsParam}&format=json&languages=${this.lang}&origin=*`;
              return this.request.getItem(getEntitiesUrl).pipe(
                map((res: any) => res && res.entities ? Object.values(res.entities) as WikibaseEntity[] : [])
              );
            });
            return requests.length > 0 ? forkJoin(requests).pipe(
              map(results => results.flat())
            ) : of([]);
          }),
          map((entities: WikibaseEntity[]) => ({ entities, searchTerm, showInDescription }))
        );
      }),
      map(({ entities, searchTerm, showInDescription }) => {
        const lang = this.lang.selectedLang;
        const translated = this.setLanguage.item(entities, lang);
        return translated.filter((item: any) => {
          const label = item.label?.toLowerCase() || '';
          const aliases = (item.aliases || []).map((a: string) => a.toLowerCase());
          const desc = item.description?.toLowerCase() || '';
          if (!searchTerm) return true;
          if (label.includes(searchTerm) || aliases.some(alias => alias.includes(searchTerm))) {
            return true;
          }
          if (showInDescription && desc.includes(searchTerm)) {
            return true;
          }
          return false;
        });
      })
    ).subscribe((items: WikibaseEntity[]) => {
      this.items = items;
      this.items$.next(items);
      this.isDisplay = this.items.length > 0;
      this.updateHintValue();
      if (this.items.length === 0) {
        this.items = [];
        this.items$.next([]);
        this.isDisplay = false;
      }
      this.changeDetector.detectChanges();
    });


    // Observable pour la liste filtrée
    this.filteredItems$ = combineLatest([
      this.filterInput.valueChanges.pipe(startWith('')),
      this.items$
    ]).pipe(
      map(([filter, items]) => {
        const f = (filter || '').toLowerCase();
        if (!f) return items;
        return items.filter(item =>
          (item.label && item.label.toLowerCase().includes(f)) ||
          (item.description && item.description.toLowerCase().includes(f))
        );
      })
    );


  }

  updateHintValue() {
    // On cherche le projet sélectionné dans researchFields
    const selectedField = Array.isArray(this.researchFields)
      ? this.researchFields.find((f: any) => f.id === this.selectedResearchField)
      : undefined;

    // Si des items sont présents, on affiche leur nombre
    if (this.items.length > 0) {
      this.hintValue$ = of(this.items.length);
      return;
    }

    // Sinon, si pas de projet sélectionné ou projet "all", on affiche le nombre de pages
    if (!selectedField || selectedField.id === 'Q0') {
      this.hintValue$ = this.pages;
    } else {
      this.hintValue$ = of(this.items.length);
    }
  }

  createList(re) {
    let list = "";
    let url = "";
    let arr = re.search;
    if (arr === undefined) { arr = [] }
    else { arr = arr };
    for (let i = 0; i < arr.length; i++) {
      list = list + "|" + arr[i].id;
    };
    list = list.slice(1);
    url = this.baseGetURL + list + this.getUrlSuffix;
    return url
  }

  addParis(re) {
    re = "Paris, " + re;
  }

  listFromSparql(res) {
    if (res !== undefined) {
      if (res.results !== undefined) {
        for (let i = 0; i < res.results.bindings.length; i++) {
          res.results.bindings[i]["item"].id = res.results.bindings[i]["item"].value.replace(
            "https://database.factgrid.de/entity/", "");
          res.results.bindings[i]["item"].id.startsWith("P") ? res.results.bindings[i]["item"].entity = "property" : res.results.bindings[i]["item"].entity = "item";
          res.results.bindings[i]["itemLabel"].value = res.results.bindings[i]["itemLabel"].value;
          if (res.results.bindings[i]["itemDescription"]) {
            res.results.bindings[i]["itemDescription"].value = res.results.bindings[i]["itemDescription"].value;
          } else {
            res.results.bindings[i]["itemDescription"] = { value: '' };
          }
        }
      }
    }
    else {
      res = { head: { vars: ['item', 'itemLabel', 'itemDescription'] }, results: { bindings: [] } }
    }
    return res
  }

  researchFieldSelect(researchField: any) {
    if (!researchField) {
      this.selectedResearchField = 'all';
      localStorage['selectedResearchField'] = 'all';
    } else {
      this.selectedResearchField = researchField.id;
      localStorage['selectedResearchField'] = this.selectedResearchField;
      this.updateHintValue();
      if (researchField.name === 'Paris') {
        return;
      }
      if (researchField.name === 'Harmonia Universalis') {
        return;
      }
    }
  }

  public displayResearchField(researchField: any): string {
    if (typeof researchField === 'string') {
      return researchField;
    }
    return researchField && researchField.name ? researchField.name : '';
  }

  clearProjectSearch() {
    this.searchResearchField.setValue('');
    this.selectedResearchField = '';
    this.updateHintValue();
  }

  clearItemSearch() {
    this.searchInput.setValue('', { emitEvent: false });
    this.items = [];
    this.items$.next([]);
    this.isDisplay = false;
    this.changeDetector.detectChanges();
  }

  onShowInDescriptionChange(checked: boolean) {
    this.showInDescription = checked;
    this.showInDescription$.next(checked);
  }

  ngOnDestroy(): void {
    if (this.labels) {
      this.labels.unsubscribe();
    }
  }
}
